<template>
  <div id="courseware">
    <head-top title="课件"></head-top>
    <van-pull-refresh
      v-model="loading"
      @refresh="getDataList"
      class="pull_fresh"
    >
      <div class="main">
        <!-- @click="download(item)" -->
        <div v-for="(item, index) in dataList" :key="index" class="item">
          <div class="fL">
            <i class="iconfont icon-word"></i>
            <span class="title time">{{
              item.Value["MaterialName"] + "." + item.Value["MaterialFileExt"]
            }}</span>
          </div>
          <div class="size time fR">
            <span v-if="item.Value['MaterialFileSize'] > 1024 * 1024"
              >{{
                Math.round(item.Value["MaterialFileSize"] / 1024.0 / 1024)
              }}
              MB</span
            >
            <span v-else
              >{{ Math.round(item.Value["MaterialFileSize"] / 1024) }} KB</span
            >

            <van-icon
              class-prefix="iconfont icon-yulan"
              @click.stop.prevent="previewFile($event, item)"
            />
            <van-icon
              class-prefix="iconfont icon-xiazai"
              @click.stop.prevent="download(item)"
            />
          </div>
        </div>

        <van-loading v-if="loading" type="spinner" color="#1989fa"
          >加载中...</van-loading
        >
        <r-lack-data
          v-else-if="!dataList || dataList.length === 0"
        ></r-lack-data>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { Loading, Icon, Dialog } from "vant";

import headTop from "@/components/headTop";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: {
    headTop,
    [Loading.name]: Loading,
    [Icon.name]: Icon,
    [Dialog.name]: Dialog
  },
  props: {
    routeType: {
      type: String,
      default: "course"
    }
  },
  data() {
    return {
      dataList: [],
      loading: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.loading = true;
      this.$mainHttp.courseware
        .getList({
          courseID: this.$store.getters.courseID,
          attribute: 2
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    download(item) {
      let fileUrl = this.$mainHttp.resourse.file(item.Value["MaterialID"]);
      console.log(fileUrl);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.$toast("下载开始");
      } else {
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {})
          .catch(err => {});
      }
    },
    previewFile(e, item) {
      // &&        [          "txt",          "pdf",          "doc",          "docx",          "xls",          "csv",          "xlsx",          "ppt",          "pptx"        ].indexOf(item.Value["MaterialFileExt"]) > -1
      if (item.Value["MaterialFileSize"] > 1024 * 1024 * 15) {
        Dialog.confirm({
          title: "提示",
          message: "超过15MB的文档，建议您下载到本地查看",
          confirmButtonText: "关闭",
          cancelButtonText: "下载",
          cancelButtonColor: "#909399"
        })
          .then(() => {
            // on confirm
          })
          .catch(() => {
            this.download(item);
          });
      } else {
        this.toPreviewUrl(item);
      }
    },
    toPreviewUrl(item) {
      if (this.routeType === "course") {
        this.$router.push(
          `/coursewareView/${item.Value["MaterialID"]}/${item.Value["MaterialFileExt"]}/${item.Value["MaterialName"]}`
        );
      } else {
        this.$router.push(
          `/history/coursewareView/${item.Value["MaterialID"]}/${item.Value["MaterialFileExt"]}/${item.Value["MaterialName"]}`
        );
      }
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang="less" scoped>
@import "pdfh5/css/pdfh5.css";
#courseware {
  width: 100%;
  height: 100%;
  .main {
    width: 100%;
    background: #ffffff;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    .item {
      overflow: hidden;
      width: 96vw;
      height: 6vh;
      line-height: 6vh;
      margin: 0 auto;
      border-bottom: 1px solid #dddddd;
      span,
      i {
        display: inline-block;
      }
      .iconfont {
        font-size: 1.65rem;
      }

      .icon-word {
        color: #409eff;
      }
      .icon-yulan {
        padding-left: 1vw;
        color: #07c160;
      }
      .icon-xiazai {
        padding-left: 1vw;
        color: #ff976a;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem);
    }
  }
}
</style>



